import React from 'react';
import Work from './Work';
import mysql from '../images/mysql.png';
import js from '../images/js.png';
import python from '../images/python.png';


const workCards = () => {
  return (
    <section className="work">
      <h2 className="heading">My Work</h2>
    <div className='container-fluid d-flex justify-content-center'>
      <div className='row'>
        <div className='col-md-4'>
          <Work
            image={js}
            title={'Meet the team'}
            text={'This project was made using html, css and vanilla js'}
            btn={'Project 1'}
          />
        </div>
        <div className='col-md-4'>
          <Work
            image={mysql}
            title={'Blog Website'}
            text={'My first full stack website'}
            btn={'Project 2'}
          />
        </div>
        <div className='col-md-4'>
          <Work
            image={python}
            title={'Lottery'}
            text={'A python lottery game, where you guess 6 random numbers.'}
            btn={'Project 3'}
          />
        </div>
      </div>
      </div>
    </section>
  );
};

export default workCards;
