import React, { useState} from 'react';
import { Modal, Button } from 'react-bootstrap';
import './Home.css';

const Home = () => {
  const [show, setShow] = useState(false);

  const handleShow = () => setShow(true);

  const handleClose = () => setShow(false);

  return (
    <>
      <div>
        <input type='checkbox' id='check' />
        <header>
          <label htmlFor='check'>
            <i className='fas fa-bars' id='sidebar_btn' />
          </label>
          <div className='left_area'>
            <h3>
              A<span>NAM</span>
            </h3>
          </div>
        </header>
        <div className='sidebar'>
          <center>
            <img
              src={require('../images/photo.jpg')}
              className='profile_image'
              alt='img'
            />
            <h4>Anam</h4>
          </center>
          <a href='/home'>
            <i className='fas fa-home' />
            <span>Home</span>
          </a>
          <a href='/about'>
            <i className='fas fa-info-circle' />
            <span>About</span>
          </a>
          <a href='/'>
            <i className='fas fa-layer-group' />
            <span>Projects</span>
          </a>
          <a href='/'>
            <i className='fas fa-desktop' />
            <span>Skills</span>
          </a>
          <a href='/'>
            <i className='fas fa-file-word' />
            <span>My CV</span>
          </a>
          <a href='/'>
            <i className='fas fa-id-card-alt' />
            <span>Contact</span>
          </a>
        </div>
      </div>

      <div className='content'>
        <div className='d-flex align-items-center justify-content-center'>
          <button
            type='btn-info'
            className='btn-lg'
            variant='primary'
            onClick={handleShow}>
            Greetings
          </button>
        </div>
        <Modal
          aria-labelledby='contained-modal-title-vcenter'
          centered
          show={show}>
          <Modal.Header closeButton onClick={handleClose}>
            <Modal.Title className='modal-text-center'>
              Hi, I'm Anam Majikijela...
            </Modal.Title>
          </Modal.Header>
          <Modal.Body> I am a Developer!
           <img src={require('../images/Me.jpg')} alt="img"/>
          </Modal.Body>
          <Modal.Footer>
            <Button variant='secondary'><a href="/about">Let's get started</a></Button>
          </Modal.Footer>
        </Modal>
      </div>
    </>
  );
};

export default Home;
